:root {
  --main-color: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

body {
  margin: 0;
}

.header {
  text-transform: uppercase;
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  font-size: 12px;
  display: flex;
}

ul {
  list-style-type: none;
}

.nav {
  height: 60px;
  line-height: 60px;
}

.nav_item {
  gap: 20px;
  list-style: none;
  display: inline-block;
}

.nav_link {
  color: #000;
  height: 60px;
  padding: 23px 0;
  font-weight: 500;
  text-decoration: none;
}

.nav_link:hover {
  color: var(--main-color);
}

.is-active {
  color: var(--main-color);
  position: relative;
}

[data-qa="hover"]:hover {
  color: var(--main-color);
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav_list {
  gap: 20px;
  margin: 0;
  padding: 0;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
  margin: 0;
  padding: 0;
}
